/* Slider */

.homeslider {
	background: #fff url('images/loading.gif') no-repeat center center;
	border: 5px solid #fff;
	min-height: 350px;
	margin-bottom: 2px;
	box-shadow: 0 1px 4px rgba(0, 0, 0, 0.27), 0 0 40px rgba(0, 0, 0, 0.06) inset;
}


/* Standard Department Slider */

.deptslide {
	min-height: 100%;
	max-height: 100%;
	background-image: url(images/loading.gif);
	background-position: center center;
	background-repeat: no-repeat;
}

.deptslide .caption {
    position: relative;
    bottom: 10px;
    left: 20px;
    background-color: #fff;
    opacity: .8;
    font-size: 1.3em;
    line-height: 1.4;
    z-index: 500;
}

.slideshow {
	width:  98%;
	padding: 1%;
	height: auto;
	border:  1px solid #e6f5ff;
	margin-bottom: 15px;
}

.slideshow img {
	width: 100%;
	min-height: 340px;
	max-height: 340px;
	position: relative;
	z-index: 1;
}

.slideshow ul {
	list-style-type: none;
	margin: 0px;
	padding: 0px;
}

.slideshow ul li {
	list-style-type: none;
	margin: 0px;
	padding: 0px;
}

.slideshow ul li a {
	text-decoration: none !important;
	color: #fff;
	display: block;
	position: relative;
	z-index: 1;
}

.slideshow ul li a:hover {
	text-decoration: none !important;
}

.slideshow .slidecaption {
	position: absolute;
	z-index: 20;
	bottom: 10px;
	background-color: #fff;
	opacity: .9;
	padding: 5px 10px;
}

.slideshow .slidecaption h3 {
	color: #002147;
	margin: 0px;
	padding: 0px;
	font-size: 1.7em;
	line-height: normal;
	font-family: 'trajan-pro', georgia, sans-serif;
	font-weight: normal;
	margin-bottom: 5px;
}

.slideshow .slidecaption p {
	margin: 0px;
	padding: 0px;
	line-height: 1.3;
	font-size: 1.5em;
	color: #01345f;
}